<template>
  <div id="app">
    title: 
    <input
      v-model="title"
      type="text"
    >
    : <input
      v-model="content"
      type="text"
      @keyup="sendcontent"
    >
    <div
      v-for="(item, idx) in recvList"
      :key="idx"
    >
      <h3>title: {{ item.title }}</h3>
      <h3>: {{ item.content }}</h3>
    </div>
  </div>
</template>

<script>
import Stomp from 'webstomp-client'
import SockJS from 'sockjs-client'

export default {
  name: 'App',
  data() {
    return {
      title: "",
      content: "",
      recvList: []
    }
  },
  created() {
    // App.vue    .
    this.connect()
  },
  methods: {
    sendcontent (e) {
      console.log("keyup: " + e.keyCode);
      if(e.keyCode === 13 && this.title !== '' && this.content !== ''){
        
        this.send()
        this.send2()
        this.content = ''
      }
    },    
    send() {
      console.log("Send content:" + this.content);
      console.log(this.stompClient);
      console.log(this.stompClient.connected);
      if (this.stompClient && this.stompClient.connected) {
        const msg = { 
          title: this.title,
          content: this.content 
        };
        this.stompClient.send("/receive", JSON.stringify(msg), {});
      }
    },
    send2() {
      console.log("Send content2222222:" + this.content);
      if (this.stompClient && this.stompClient.connected) {
        const msg = { 
          title: this.title,
          content: this.content 
        };
        this.stompClient.send("/receive2", JSON.stringify(msg), {});
      }
    },       
    connect() {
      const serverURL = "http://localhost:8080/socket"
      let socket = new SockJS(serverURL);
      this.stompClient = Stomp.over(socket);
      console.log(`  .  : ${serverURL}`)
      this.stompClient.connect(
        {},
        frame => {
          //   
          this.connected = true;
          console.log('  ', frame);
          //    endpoint .
          //  pub sub  .
          this.stompClient.subscribe("/send", res => {
            console.log('   .', res.body);

            //   json   .
            this.recvList.push(JSON.parse(res.body))
          });
          this.stompClient.subscribe("/send2", res => {
            console.log('222222222222', res.body);
          });
        },
        error => {
          //   
          console.log('  ', error);
          this.connected = false;
        }
      );        
    }
  }
}
</script>